.n-wrapper {
  height: 10vh;
  display: flex;
  justify-content: space-between;
}

.n-left {
  flex: 1 1;
  display: flex;
  align-items: center;
  grid-gap: 7.5rem;
  gap: 7.5rem;
}

.n-name {
  font-size: 1.3rem;
  font-weight: bold;
}

.n-expand {
  display: none;
}

.n-right {
  display: flex;
  flex: 1 1;
  align-items: center;
  justify-content: center;
  font-weight: 400;
}

.n-list {
  flex: 10 1;
}

.n-list > ul {
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  margin-right: 4rem;
  cursor: pointer;
}

.n-list > ul > li:hover {
  cursor: pointer;
  color: var(--orange);
}

.n-button {
  flex: 2 1;
}

@media screen and (max-width: 480px) {
  .n-left > :nth-child(2) {
    margin: 0.9rem;
  }
  .n-wrapper {
    height: 2rem;
    grid-gap: 2rem;
    gap: 2rem;
  }
  .n-list ul {
    display: block;
    line-height: 2rem;
    flex-direction: column;
    position: absolute;
    width: 90%;
    top: 7%;
    left: 0;
    background-color: #fca61f;
    color: black;
  }

  .showed ul {
    display: none;
  }

  .n-list li {
    display: block;
    text-align: center;
    margin: 0;
    width: 90%;
    padding: 0.5rem 0;
  }
  .n-list li:hover {
    color: white;
  }
  .n-button {
    display: none;
  }
  .n-expand {
    display: inline;
    border: none;
    background-color: white;
  }
  /* Add this new class */
  .n-expand-animate {
    transition: -webkit-transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out;
    transition: transform 0.3s ease-in-out, -webkit-transform 0.3s ease-in-out;
  }

  /* Use this class to rotate the icon when the menu is open */
  .n-expand-animate.open {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
}

